import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Box from '@mui/material/Box';
import { BlogPost } from '../Service/DataService';
import { imagePath, splitStringToLines, stringsArrayToString } from '../Tools/Functions';
import Button from '@mui/material/Button';

type MainFeaturedPostProps = {
  post: BlogPost
};

export default function MainFeaturedPost(props: MainFeaturedPostProps) {
  const { post } = props;

  return (
    <Paper
      sx={{
        position: 'relative',
        backgroundColor: 'grey.800',
        color: '#fff',
        mb: 4,
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        backgroundImage: `url(${imagePath(post.image)})`,
      }}
    >
      {/* Increase the priority of the hero background image */}
      {<img style={{ display: 'none' }} src={imagePath(post.image)} alt={post.image} />}
      <Box
        sx={{
          position: 'absolute',
          top: 0,
          bottom: 0,
          right: 0,
          left: 0,
          backgroundColor: 'rgba(0,0,0,.3)',
        }}
      />
      <Grid container>
        <Grid item md={6}>
          <Box
            sx={{
              position: 'relative',
              p: { xs: 3, md: 6 },
              pr: { md: 0 },
            }}
          >
            <Typography component="h1" variant="h3" color="inherit" gutterBottom>
              {post.title}
            </Typography>
            <Typography variant="h5" color="inherit" paragraph>
              {stringsArrayToString([
                ...splitStringToLines(post.content).slice(0, 3)
              ])}
            </Typography>

            <Link variant="subtitle1" href={'/post/' + post.id}>
              <Button variant="outlined" style={{
                background: 'white',
              }}>Get excited!</Button>
            </Link>
          </Box>
        </Grid>
      </Grid>
    </Paper >
  );
}
